<template>
  <div>
    <div class="layout">
        <Layout :style="{height: '100%'}" v-if="menu.length != 0">
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo" @click="menuIndexCh"></div>
                    <div class="layout-nav">
                        <Menu @on-select="changeTab" mode="horizontal" theme="dark" active-name="1">
                            <MenuItem v-for="(it,$index) in menu" :key="$index" :name="$index">
                                <Icon type="ios-paper" />
                                {{it.MenuName}}
                            </MenuItem>
                        </Menu>
                    </div>
                </Menu>
            </Header>
            <Layout :style="{height: '100%'}">
                <Sider v-for="(it,$index) in menu" :key="$index" v-show="menuIndex === $index * 1" hide-trigger :style="{width: '225px', maxWidth: 'auto', flex: '0 0 225px', height: '100%', overflow: 'auto', 'overflowX': 'hidden'}">
                    <Menu :active-name="menuItem" :open-names="menuActive" theme="dark" width="225px" accordion>
                        <Submenu :name="m.Id" v-for="(m,$m) in it.subs" :key="$m">
                            <template slot="title">
                                <Icon type="ios-navigate"></Icon>
                                {{m.MenuName}}
                            </template>
                            <MenuItem v-for="(s,$s) in m.subs" :key="$s" to="/terminal/list" name="1-1">{{s.MenuName}}</MenuItem>
                        </Submenu>
                    </Menu>
                </Sider>
                <Layout :style="{padding: '0 24px 0px'}">
                    <Breadcrumb :style="{margin: '24px 0'}">
                        <BreadcrumbItem :to="item.path == '' ? '/' : item.path" v-for="(item, $index) in breadcrumb_item" :key="$index">{{item.meta.title}}</BreadcrumbItem>
                    </Breadcrumb>
                    <Content :style="{padding: '24px', height: 'auto', background: '#fff'}">
                        <router-view :style="{width: '100%', height: '100%'}"></router-view>
                    </Content>
                    <p :style="{lineHeight: '42px'}" class="content-footer">2019 © Devon. Ver:2.0.1</p>
                </Layout>
            </Layout>
        </Layout>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'menuActive',
      'menuItem',
      'menu'
    ])
  },
  watch: {
    $route ( router ) {
      // alert(val)
      console.log(router)
      this.breadcrumb_item = router.matched
    }
  },
  data () {
    return {
      menuIndex: 0,
      breadcrumb_item: [],
      MENU: {
        yunying: [{
          title: 'STEP1',
          menu: [{
            title: 'step-1'
          },{
            title: 'step-2'
          }]
        },{
          title: 'STEP2',
          menu: [{
            title: 'step-1'
          },{
            title: 'step-2'
          }]
        }],
        yingxiao: [{
          title: 'STEPA',
          menu: [{
            title: 'step-1'
          },{
            title: 'step-2'
          }]
        },{
          title: 'STEPB',
          menu: [{
            title: 'step-1'
          },{
            title: 'step-2'
          }]
        }],
        caiwu: [],
        shuju: [],
        xitong: []
      }
    }
  },
  created () {
    this.breadcrumb_item = this.$route.matched
  },
  methods: {
    menuIndexCh () {
      if (this.menuIndex) {
        this.menuIndex = 0
      } else {
        this.menuIndex = 1
      }
    },
    changeTab ( val ) {
      this.menuIndex = val * 1
    }
  }
}
</script>

